@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
v2.0 | 20110126
License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  color: inherit;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* TODO: 임시 */

html {
  font-size: calc(100vw / 360);
}
* {
  font-family: 'Montserrat', 'Noto Sans KR', sans-serif;
  color: #333;
  box-sizing: border-box;
}
img {
  max-width: 100%;
}

body {
  min-width: 360px;
}
a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  border: 1px solid transparent;
}
a:focus {
  border: 1px dashed gray;
}
button {
  border: none;
  cursor: pointer;
  background: transparent;
  padding: 0;
}

body > #root {
  min-width: 360px;
  min-height: 100vh;
  padding-bottom: 50rem;
}
.App-header h1 {
  font-size: 20rem;
  margin: 16rem;
}
/* modal 관련 */
#modal {
}
/* 공통 css 클래스 */
/* 헤더가 있는 섹션 클래스 */
.header_section {
  padding-top: 45rem;
}
.fixed_btn_section {
  overflow: auto;
  height: calc(100vh - 50rem);
}
/* 리더기 용text 히든 클래스 */
.hidden {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  white-space: nowrap;
}

